<template>
  <div
    class="w-100 h-100 d-flex align-center justify-center overflow-hidden"
    style="position: relative"
  >
    <img-background style="position: absolute"></img-background>
    <div class="introWrap d-flex align-center justify-center">
      <v-btn
        v-if="showBtn"
        rounded="lg"
        variant="outlined"
        class="blinking mr-1 ml-4"
        @click="playIntro"
        min-width="42px"
        min-height="42px"
        width="42px"
        height="42px"
        ><v-icon icon="mdi-replay"></v-icon
      ></v-btn>
      <h1 class="introText px-1">
        {{ introText }}
      </h1>
    </div>
  </div>
</template>
<script>
import imgBackground from '../../components/ImgBgComponent.vue'
export default {
  components: { imgBackground },
  data() {
    return {
      // 인트로 글자 모음
      letters: [
        '안녕하세요.',
        '항상 배우는 자세로 정진하는',
        // '사용자 관점에서 한번 더 생각하는',
        '4년차 웹 프론트엔드 개발자 김용성 입니다.'
      ],
      typingSpeed: 100, // 인트로 글자 입력 속도
      removeSpeed: 70, // 인트로 글자 삭제 속도
      introText: '',
      curType: 0,
      showBtn: false
    }
  },
  setup() {},
  created() {},
  mounted() {
    setTimeout(this.playIntro, 300)
  },
  unmounted() {},
  methods: {
    playIntro() {
      this.curType = 0
      this.introText = ''
      this.showBtn = false
      this.typing()
    },
    // 타이핑 효과
    async typing() {
      const letter = this.letters[this.curType].split('')

      while (letter.length) {
        await this.wait(this.typingSpeed)
        this.introText += letter.shift()
      }

      // 잠시 대기
      await this.wait(800)

      // 지우는 효과
      if (this.letters[this.curType + 1]) {
        this.remove()
      } else {
        this.showBtn = true
      }
    },
    // 글자 지우는 효과
    async remove() {
      // const letter = this.letters[this.curType].split('')

      // while (letter.length) {
      //   await this.wait(this.removeSpeed)

      //   letter.pop()
      //   this.introText = letter.join('')
      // }

      // // 다음 순서의 글자로 지정, 타이핑 함수 다시 실행
      // this.curType = !this.letters[this.curType + 1] ? 0 : this.curType + 1
      // this.typing()

      this.introText = ''
      await this.wait(this.removeSpeed)

      // 다음 순서의 글자로 지정, 타이핑 함수 다시 실행
      this.curType = !this.letters[this.curType + 1] ? 0 : this.curType + 1
      this.typing()
    },
    // 딜레이 기능 ( 마이크로초 )
    wait(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms))
    }
  }
}
</script>
<style>
.introWrap {
  z-index: 10;
  width: 80%;
  height: 200px;
  /* Fallback color */
  /* background-color: rgb(0, 0, 0); */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/opacity/see-through */
  color: white;
  font-weight: bold;
  border: 3px solid #f1f1f1;
  position: relative;
}
.introText {
  font-size: 1.8rem;
  text-align: center;
  word-break: keep-all;
}
.introText::after {
  content: '';
  margin-left: 0.4rem;
  /* border-right: 2px solid #999896; */
  /* border-right: 2px solid #494846; */
  border-right: 2px solid white;
  /* animation: cursor 0.7s infinite steps(2); */
  animation: cursor 0.8s linear infinite;
}
.introWrap .v-btn {
  position: absolute;
  top: 10px;
  left: cal(50% - 21px);
}
@keyframes cursor {
  from {
    /* border-right: 2px solid #494846; */
    opacity: 1;
  }
  to {
    /* border-right: 2px solid #999896; */
    opacity: 0.1;
  }
}

